@import "https://fonts.googleapis.com/css2?family=Baloo+Paaji+2:wght@400;500&display=swap";
.container {
  grid-gap: 50px;
  min-height: 100vh;
  min-width: 100vw;
  background-color: #524eb7;
  grid-template-columns: 300px 300px 300px;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: "Baloo Paaji 2", cursive;
  display: grid;
}

.card {
  height: 37rem;
  box-shadow: #000000b3;
  color: #fff;
  background-color: #222831;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
  display: flex;
}

.active {
  background-color: #f04141;
}

.choose-btn {
  color: #fff;
  cursor: pointer;
  background-color: #524eb7;
  border: none;
  padding: 8px 16px;
}

.image {
  height: 160px;
  width: 160px;
  border: 5px solid #272133;
  border-radius: 50%;
  margin-top: 20px;
  box-shadow: 0 10px 50px #eb196e;
}

